/**
                                                                                                                              * Uses mobiscroll locale to format a valid date in the required language
                                                                                                                              * @param {} date
                                                                                                                              * @param {} fixedDateFormat
                                                                                                                              * @param {} language
                                                                                                                              * @returns {}
                                                                                                                              */
bizagi.util.localeDate = function (date, fixedDateFormat, language) {

  if (typeof $.mobiscroll.i18n[language] !== "undefined") {
    if (fixedDateFormat.indexOf("tt") !== -1) {
      fixedDateFormat = fixedDateFormat.replace("tt", "a");
    }

    // Checks if the format includes MM
    if (fixedDateFormat.indexOf("MM") !== -1) {
      // Checks if the full month is included in the format
      if (fixedDateFormat.indexOf("MMMM") !== -1) {
        fixedDateFormat = fixedDateFormat.replace("MMMM", "MM");
      } else {
        //Checks the month is in number format
        if (fixedDateFormat.indexOf("MM") !== -1) {
          fixedDateFormat = fixedDateFormat.replace("MM", "mm");
        }
      }
    }

    //Fix long day (Monday, Tuestday...)
    if (fixedDateFormat.indexOf("dddd") !== -1) {
      fixedDateFormat = fixedDateFormat.replace("dddd", "DD");
    }

    var format = $.mobiscroll.formatDate(fixedDateFormat, date, $.mobiscroll.i18n[language]);

    return bizagi.util.convertHexNCR2Char(format);
  }

  return date;
};

/**
    * Converts a string containing &#x...; escapes to a string of characters
    * @params: str: string, the input
    */
bizagi.util.convertHexNCR2Char = function (str) {
  // str: string, the input

  // convert up to 6 digit escapes to characters
  str = str.replace(/&#x([A-Fa-f0-9]{1,6});/g,
  function (matchstr, parens) {
    return bizagi.util.hex2char(parens);
  });

  return str;
};

/**
    * Converts a single hex number to a character
    * @params: hex: string, the hex codepoint to be converted
    */
bizagi.util.hex2char = function (hex) {
  // note that no checking is performed to ensure that this is just a hex number, eg. no spaces etc
  var result = "";
  var n = parseInt(hex, 16);

  if (n <= 0xFFFF) {
    result += String.fromCharCode(n);
  } else if (n <= 0x10FFFF) {
    n -= 0x10000;
    result += String.fromCharCode(0xD800 | n >> 10) + String.fromCharCode(0xDC00 | n & 0x3FF);
  } else {
    //result += 'hex2Char error: Code point out of range: ' + dec2hex(n);
    result += "hex2Char error: Code point out of range";
  }

  return result;
};

/**
    *Apply currency format  
    * @param {} value 
    * @returns {} 
    */
bizagi.util.formatMonetaryCell = function (value) {
  return bizagi.util.formatMoney(value, BIZAGI_DEFAULT_CURRENCY_INFO);
};

/*
   * Format monetary values, the format must have the structure of the next example:
   *
   * var defaultFormat = {
   *        symbol: "€",
   *        decimalSeparator: ",",
   *        groupSeparator: ".",
   *        decimalDigits: "2"
   *    };
   */
bizagi.util.formatMoney = function (value, format) {
  var result = "";
  var tmpResult = "";
  var intSection = "";
  var decimalSection = "";
  var decimalResult = "";

  if (value != undefined)
  value = value.toString();else

  value = "0";

  if (value.length >= 1) {
    var decimalDigitPosition = value.indexOf(".");

    if (decimalDigitPosition > 0) {
      value = parseFloat(value).toFixed(format.decimalDigits);
      intSection = value.substring(0, decimalDigitPosition);
    } else {
      var zeroChar = "0";

      for (var i = 0; i < format.decimalDigits; i++) {
        decimalResult += zeroChar;
      }
      intSection = value.replace(/\D/g, '');
    }

    //take only the number characters removing other characters
    value = value.replace(/\D/g, '');
    //take the decimal section
    if (format.decimalDigits > 0) {
      decimalSection = decimalDigitPosition > 0 ?
      format.decimalSeparator + value.substring(value.length - format.decimalDigits, value.length) :
      format.decimalSeparator + decimalResult;
    } else {
      decimalSection = "";
    }
    //insert groupSeparators into int section
    if (intSection.length > 3) {
      var count = 0;

      //insert separator from end to begin
      for (var i = intSection.length - 1; i >= 0; i--) {
        if (count == 3) {
          tmpResult += ".";
          count = 0;
        }
        tmpResult += intSection[i];
        count += 1;
      }
      //revert the string to get the result value
      for (var i = tmpResult.length - 1; i >= 0; i--) {
        result += tmpResult[i];
      }
    } else {
      result = intSection;
    }

    //replace the correct group separator
    result = result.replaceAll(".", format.groupSeparator);
    //unify all sections and finish format
    result = format.symbol + result + decimalSection;

    return result;
  } else {
    return value;
  }
};

