/* 
 * Custom Loading Message 
 * @author: Edward J Morales, Refactor => Diego Parra
 */

/**
     * 
     * @param: type  {default || custom}
     * @param: customMessage  {html}
     * @param: positionX  {integer+px|| percent}
     * @param: positionY  {integer+px|| percent}
     */

(function ($) {
  // Create or Define BizAgi namespace
  bizagi = typeof bizagi !== "undefined" ? bizagi : {};
  var bizagiLoaderMessageTemplate = $.template('#loading_template', '<div class="ui-bizagi-loading-message" style="position: relative;"><div class="ui-bizagi-loading-message-center" style="position: ${position}; top: ${top}; left: ${top}; display: ${inline};"><div class="ui-bizagi-loading-icon"></div></div></div>');

  // Loading plugin
  $.fn.loadingMessage = function (options) {
    options = options || {};

    // define global options
    var parentContainer = this;

    // Don't set the waiting message if the container has elements
    if (parentContainer.children().length == 0) {
      var message = $.fasttmpl(bizagiLoaderMessageTemplate, {
        "position": "absolute",
        "top": options["position-y"] || "48%",
        "left": options["position-x"] || "48%",
        "display": "inline" });

      parentContainer.append(message);
      parentContainer.data("loading", true);
    }
  };

  $.fn.endLoadingMessage = function () {
    var parentContainer = this;
    if (parentContainer.children().length > 0) {
      if ($('> .ui-bizagi-loading-message', parentContainer).length) {
        parentContainer.data('loading', false);
        parentContainer.attr('data-loading', false);
        parentContainer.removeClass('ui-bizagi-component-loading');
        parentContainer.empty();
      }
    }
  };

  (function ($) {
    $.fn.startLoading = function (params) {
      var content = this;
      var params = params || {};
      var delay = params.delay || 0;
      var overlay = params.overlay || false;
      var opacity = params.opacity || 0;
      var maxZIndex = params.maxZIndex || false;

      // Check that the container is not loading already
      if ($(content).data("loading") == true) return;

      // Set loading flag
      $(content).data("loading", true);

      // Create overlay
      var overlayElement;
      if (overlay) {
        if (opacity) {
          overlayElement = $.template('#loading_tmpl_transparent', '<div class="ui-bizagi-loading-transparent-overlay ui-bizagi-loading-gray-overlay" style="background-color:rgba(128, 128, 128, ${opacity});opacity:${opacity};z-index:${zindex};"><div class="ui-bizagi-loading-overlay-preloader"></div></div>');
        } else {
          overlayElement = $.template('#loading_tmpl_transparent', '<div class="ui-bizagi-loading-transparent-overlay ui-bizagi-loading-alpha-overlay" style="z-index:${zindex};"><div class="ui-bizagi-loading-overlay-preloader"></div></div>');
        }
      } else {
        overlayElement = $.template('#loading_tmpl_alpha', '<div class="ui-bizagi-loading-transparent-overlay" style="z-index:${zindex};"><div class="ui-bizagi-loading-overlay-preloader ${extraCssClass}"></div></div>');
      }

      maxZIndex = maxZIndex ? $.ui.dialog.prototype._getMaxZindex() : 'auto';

      // Creates a timeout function and save the handle in the element
      var handle = window.setTimeout(function () {
        $(content).data("original-position", $(content).css("position"));
        $(content).css("position", "relative");

        if (overlay) {
          $(content).append($.fasttmpl(overlayElement, { opacity: opacity, zindex: maxZIndex }));
        } else {
          $(content).append($.fasttmpl(overlayElement, { zindex: maxZIndex }));
        }

        // Update the display property to control
        changeDisplayToContent(content, true);

        $(content).removeData("loading-timeout");
      }, delay);
      $(content).data("loading-timeout", handle);
    };

    $.fn.endLoading = function () {
      var content = this;
      $(content).css("position", $(content).data("original-position"));
      $(".ui-bizagi-loading-transparent-overlay", content).remove();

      // Update the display property to control
      changeDisplayToContent(content, false);

      // Clear the timeout handle
      var handle = $(content).data("loading-timeout");
      if (handle) clearTimeout(handle);

      // Remove loading flag
      $(content).data("loading", false);

    };

    function changeDisplayToContent(content, updateToInitial) {
      if ($(content).css("display") === "table") {
        var $sibling = $(".ui-bizagi-loading-transparent-overlay.ui-bizagi-loading-alpha-overlay", content).prev();

        if ($sibling && $sibling.length > 0) {
          if (updateToInitial) {
            $(content).data("original-display", $sibling.css("display"));
            if (bizagi.util.isSharepointContext() || bizagi.util.isWebpartContext()) {
              $sibling.css("display", "table-cell");
            } else
            {
              $sibling.css("display", "inline");
            }
          } else {
            var displayType = $(content).data("original-display");
            if (displayType) {
              $sibling.css("display", displayType);
            }
          }
        }
      }
    }

  })(jQuery);

})(jQuery);